
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse' 
import backTop from '@/components/BackTop/backTop'
import showMore from '@/components/ShowMore/showMore'  
export default{
data() {
return {
ny: "",
yljgid: "",
jzlsh: "",
info: {},
}
},
mounted() {
this.getParams();
},
methods: {

getParams() {
var routerParams = this.$route.params;
this.ny = routerParams.NY;
this.yljgid = routerParams.YLJGID;
this.jzlsh = routerParams.JZLSH;
this.getMedicalRecords();
},

getMedicalRecords() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "outpatientDepartmentLog",
"MSH.2": "bl",
"CHZBM": this.$root.loginInfo.CHZBM,
"yljgid": this.yljgid,
"jzlsh": this.jzlsh,
"ny": this.ny,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
},
components: {
XHeader,Collapse,backTop,showMore
},
}




